        ,
        activeMenu() {
            return Object.keys(this.menus).reduce(($$, set, i) => (this.menus[set]) ? i : $$, -1)
        },
        list() {
            var counter_filters = 0
            let { business , period , indicators, detail_level, report_format} = this.activeFilters
            var obj_filters = {business , period, indicators, detail_level, report_format}
            for (var n in obj_filters) {
                (obj_filters[n].length !== 0) ? counter_filters += 1 : counter_filters = counter_filters
            }
            return this.list_reports.filter(function(e){
                var counter_instance = 0
                for (var m in obj_filters) {
                    if (obj_filters[m].length !== 0) {
                        if ($.isArray(e[m]) && e[m].contains(obj_filters[m])) {
                            counter_instance += 1
                        }
                        else if (!$.isArray(e[m]) && obj_filters[m].indexOf(e[m]) !== -1){
                            counter_instance += 1
                        }
                    }
                }
                if (counter_instance == counter_filters) {
                    return e
                } else {
                    return false
                }
            })
        },
        activeFilters() {
            let { period, business, indicators, detail_level, report_format} = this.filters
            return {
                period: Object.keys(period).filter(c => period[c]),
                business: Object.keys(business).filter(c => business[c]),
                indicators: Object.keys(indicators).filter(c => indicators[c]),
                detail_level: Object.keys(detail_level).filter(c => detail_level[c]),
                report_format: Object.keys(report_format).filter(c => report_format[c])
            }
        }
